body {
  color: #e0e0e0;
  scroll-behavior: smooth;
  background-color: #121212;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
  overflow: hidden;
}

header {
  color: #fff;
  text-align: center;
  z-index: 1000;
  background: #1f2937;
  border-bottom: 4px solid #38bdf8;
  padding: 25px 0;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 8px #00000040;
}

.logo {
  color: #fff;
  margin-bottom: 5px;
  font-size: 2.2em;
  font-weight: bold;
}

.tagline {
  opacity: .8;
  color: #e0e0e0;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.1em;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav ul li {
  margin: 0 15px;
  display: inline;
}

nav ul li a {
  color: #fff;
  text-transform: uppercase;
  font-size: .95em;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a:hover {
  color: #38bdf8;
}

section {
  background: #1e1e1e;
  margin-bottom: 0;
  padding: 60px 0;
  scroll-margin-top: 120px;
  box-shadow: 0 2px 8px #00000026;
}

section:last-of-type {
  margin-bottom: 0;
}

.alt-background {
  background: #242424;
}

h1, h2 {
  text-align: center;
  color: #fff;
  margin-bottom: 40px;
  font-weight: 600;
}

h1 {
  font-size: 2.8em;
  line-height: 1.2;
}

h2 {
  padding-bottom: 10px;
  font-size: 2.2em;
  position: relative;
}

h2:after {
  content: "";
  background-color: #38bdf8;
  border-radius: 5px;
  width: 60px;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

#hero {
  color: #fff;
  text-align: center;
  box-shadow: none;
  background: linear-gradient(#121212b3, #121212cc), url("RainierSoftwareSolutions.07f36f49.png") center / cover no-repeat;
  padding: 100px 20px;
  scroll-margin-top: 0;
}

#hero h1 {
  color: #fff;
  margin-bottom: 20px;
  font-size: 3.5em;
}

#hero p {
  opacity: .9;
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.3em;
}

.services-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 20px;
  display: grid;
}

.service-item {
  background: #2a2a2a;
  border-left: 5px solid #38bdf8;
  border-radius: 8px;
  padding: 30px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 4px 10px #0003;
}

.service-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px #0000004d;
}

.service-item h3 {
  color: #38bdf8;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.4em;
}

.service-item ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.service-item ul li {
  color: silver;
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
}

.service-item ul li:before {
  content: "✓";
  color: #22c55e;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
}

.contact-info {
  text-align: center;
  font-size: 1.1em;
}

.contact-info p {
  color: #e0e0e0;
  margin-bottom: 10px;
}

.contact-info .email-link a {
  color: #38bdf8;
  font-size: 1.2em;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s, text-decoration .3s;
}

.contact-info .email-link a:hover {
  color: #0ea5e9;
  text-decoration: underline;
}

footer {
  text-align: center;
  color: #e0e0e0;
  background: #1f2937;
  border-top: 4px solid #38bdf8;
  padding: 30px;
  font-size: .9em;
}

footer a {
  color: #fff;
  text-decoration: none;
  transition: color .3s;
}

footer a:hover {
  color: #38bdf8;
}

@media (width <= 768px) {
  .container {
    width: 95%;
  }

  header {
    padding-top: 20px;
  }

  section {
    scroll-margin-top: 100px;
  }

  #hero {
    scroll-margin-top: 0;
  }

  .logo {
    font-size: 1.8em;
  }

  .tagline {
    margin-bottom: 15px;
    font-size: 1em;
  }

  nav ul li {
    margin: 0 8px;
  }

  nav ul li a {
    font-size: .9em;
  }

  #hero {
    padding: 80px 15px;
  }

  #hero h1 {
    font-size: 2.5em;
  }

  #hero p {
    font-size: 1.1em;
  }

  h2 {
    margin-bottom: 30px;
    font-size: 1.8em;
  }

  .services-grid {
    grid-template-columns: 1fr;
  }

  .service-item {
    margin-bottom: 20px;
  }
}
