/* General Body & Container */
body {
    font-family: 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, sans-serif;
    line-height: 1.6;
    margin: 0;
    padding: 0;
    color: #e0e0e0; /* Light grey text for dark backgrounds */
    background-color: #121212; /* Dark background */
    scroll-behavior: smooth; /* Smooth scrolling for nav links */
}

.container {
    max-width: 1100px;
    margin: auto;
    padding: 0 20px;
    overflow: hidden; /* Clear floats */
}

/* Header & Navigation */
header {
    background: #1f2937; /* Darker shade for header */
    color: #ffffff;
    padding: 25px 0;
    border-bottom: 4px solid #38bdf8; /* Lighter, vibrant blue accent */
    text-align: center;
    position: sticky; /* Sticky header for navigation */
    top: 0;
    z-index: 1000; /* Ensure it stays on top */
    box-shadow: 0 2px 8px rgba(0,0,0,0.25); /* Adjusted shadow for dark theme */
}

.logo {
    font-size: 2.2em;
    font-weight: bold;
    margin-bottom: 5px;
    color: #ffffff;
}

.tagline {
    font-size: 1.1em;
    opacity: 0.8;
    margin-top: 0;
    margin-bottom: 20px;
    color: #e0e0e0;
}

nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

nav ul li {
    display: inline;
    margin: 0 15px;
}

nav ul li a {
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 0.95em;
    font-weight: bold;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #38bdf8; /* Lighter, vibrant blue for hover on dark theme */
}

/* Sections */
section {
    padding: 60px 0;
    margin-bottom: 0; /* No margin between sections, background handles spacing */
    background: #1e1e1e; /* Slightly lighter dark for sections */
    box-shadow: 0 2px 8px rgba(0,0,0,0.15); /* Subtle shadow for depth on dark */
    scroll-margin-top: 120px; /* Adjust this value based on your sticky header's height */
}

section:last-of-type {
    margin-bottom: 0;
}

.alt-background {
    background: #242424; /* Different shade of dark for alternating sections */
}

h1, h2 {
    text-align: center;
    color: #ffffff; /* White or very light color for headings */
    margin-bottom: 40px;
    font-weight: 600;
}

h1 {
    font-size: 2.8em;
    line-height: 1.2;
}

h2 {
    font-size: 2.2em;
    position: relative; /* For underline effect */
    padding-bottom: 10px;
}

h2::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background-color: #38bdf8; /* Lighter blue underline */
    border-radius: 5px;
}


/* Hero Section Specifics */
#hero {
    background: linear-gradient(rgba(18, 18, 18, 0.7), rgba(18, 18, 18, 0.8)), url('RainierSoftwareSolutions.png') no-repeat center center/cover; /* Adjusted overlay opacity */
    color: #ffffff;
    text-align: center;
    padding: 100px 20px;
    box-shadow: none; /* No shadow for hero */
    scroll-margin-top: 0; /* Hero section doesn't need offset from itself */
}

#hero h1 {
    color: #ffffff;
    font-size: 3.5em;
    margin-bottom: 20px;
}

#hero p {
    font-size: 1.3em;
    max-width: 800px;
    margin: 0 auto;
    opacity: 0.9;
}

/* Services Grid */
.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
    margin-top: 20px;
}

.service-item {
    background: #2a2a2a; /* Dark background for service items */
    padding: 30px;
    border-left: 5px solid #38bdf8; /* Lighter blue accent */
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.2); /* Adjusted shadow for dark theme */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 15px rgba(0,0,0,0.3); /* Adjusted hover shadow */
}

.service-item h3 {
    color: #38bdf8; /* Lighter blue for service titles */
    margin-top: 0;
    font-size: 1.4em;
    margin-bottom: 15px;
}

.service-item ul {
    list-style: none; /* Remove default bullets */
    padding: 0;
    margin: 0;
}

.service-item ul li {
    margin-bottom: 8px;
    padding-left: 20px;
    position: relative;
    color: #c0c0c0; /* Lighter text for list items */
}

.service-item ul li::before {
    content: '✓'; /* Custom checkmark bullet */
    color: #22c55e; /* Keep green checkmark or choose a lighter variant if needed */
    position: absolute;
    left: 0;
    top: 0;
    font-weight: bold;
}

/* Contact Section */
.contact-info {
    text-align: center;
    font-size: 1.1em;
}

.contact-info p {
    margin-bottom: 10px;
    color: #e0e0e0;
}

.contact-info .email-link a {
    color: #38bdf8; /* Lighter blue for email link */
    text-decoration: none;
    font-weight: bold;
    font-size: 1.2em;
    transition: color 0.3s ease, text-decoration 0.3s ease;
}

.contact-info .email-link a:hover {
    color: #0ea5e9; /* Slightly different shade of blue for hover */
    text-decoration: underline;
}

/* Footer */
footer {
    text-align: center;
    padding: 30px;
    background: #1f2937; /* Consistent dark shade for footer */
    color: #e0e0e0;
    border-top: 4px solid #38bdf8; /* Lighter blue accent */
    font-size: 0.9em;
}

footer a { /* Assuming there might be links in footer, though not present in HTML */
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s ease;
}

footer a:hover {
    color: #38bdf8;
}

/* Responsive Design - Adjustments for dark theme if necessary, but mostly structural */
@media (max-width: 768px) {
    .container {
        width: 95%;
    }

    header {
        padding-top: 20px;
        /* Adjust scroll-margin-top for sections if header height changes significantly on mobile */
    }
    
    section { /* Adjust scroll-margin-top for mobile if header height is different */
        scroll-margin-top: 100px; /* Example: smaller value for potentially smaller mobile header */
    }
    #hero {
        scroll-margin-top: 0; /* Hero still doesn't need it */
    }


    .logo {
        font-size: 1.8em;
    }

    .tagline {
        font-size: 1em;
        margin-bottom: 15px;
    }

    nav ul li {
        margin: 0 8px;
    }

    nav ul li a {
        font-size: 0.9em;
    }

    #hero {
        padding: 80px 15px;
    }

    #hero h1 {
        font-size: 2.5em;
    }

    #hero p {
        font-size: 1.1em;
    }

    h2 {
        font-size: 1.8em;
        margin-bottom: 30px;
    }

    .services-grid {
        grid-template-columns: 1fr; /* Stack services on small screens */
    }

    .service-item {
        margin-bottom: 20px;
    }
}